@media (min-width: 380px) {
  .main__aside {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-end;
    flex-wrap: wrap;
    width: 380px;
    height: auto;
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    margin: 0px;
    margin-bottom: 15px;

    .aside__title {
      position: relative;
      left: 10px;
      margin-left: 0px;
      margin-bottom: 12px;
      color: #4a4a4a;
      font-weight: 600;
      line-height: 12px;
    }
  
    .filter-item {
      display: flex;
      width: auto;
      height: 40px;
      margin: 0;
      margin-left: 0px;
      margin-bottom: 0px;
  
      @supports (-webkit-appearance: none) or (-moz-appearance: none) {
        input[type='checkbox'] {
          --active: #ffffff;
          --active-inner: #2196f3;
          --focus: 1px #2196f3;
          --border: #9abbce;
          --border-hover: #2196f3;
          --background: #fff;
          --disabled: #f6f8ff;
          --disabled-inner: #e1e6f9;
          -webkit-appearance: none;
          -moz-appearance: none;
          height: 21px;
          outline: none;
          display: inline-block;
          vertical-align: middle;
          position: relative;
          margin: 0;
          margin-top: 10px;
          margin-left: 8px;
          cursor: pointer;
          border: 1px solid var(--bc, var(--border));
          background: var(--b, var(--background));
          transition: border-color 0.3s, box-shadow 0.2s;
          &:after {
            content: '';
            display: block;
            left: 0;
            top: 0;
            position: absolute;
            transition: transform var(--d-t, 0.3s) var(--d-t-e, ease), opacity var(--d-o, 0.2s);
          }
          &:checked {
            --b: var(--active);
            --bc: var(--active);
            --d-o: 0.3s;
            --d-t: 0.6s;
            --d-t-e: cubic-bezier(0.2, 0.85, 0.32, 1.2);
          }
          &:disabled {
            --b: var(--disabled);
            cursor: not-allowed;
            opacity: 0.9;
            &:checked {
              --b: var(--disabled-inner);
              --bc: var(--border);
            }
            & + label {
              cursor: not-allowed;
            }
          }
          &:hover {
            &:not(:checked) {
              &:not(:disabled) {
                --bc: var(--border-hover);
              }
            }
          }
          &:focus {
            box-shadow: 0 0 0 var(--focus);
          }
          &:not(.switch) {
            width: 21px;
            &:after {
              opacity: var(--o, 0);
            }
            &:checked {
              --o: 1;
            }
          }
          & + label {
            font-size: 11px;
            font-weight: 600;
            color: #4a4a4a;
            align-self: center;
            line-height: 15px;
            display: inline-block;
            cursor: pointer;
            margin-left: 5px;
          }
        }
        input[type='checkbox'] {
          &:not(.switch) {
            border-radius: 10px;
            &:after {
              width: 5px;
              height: 9px;
              border: 2px solid var(--active-inner);
              border-top: 0;
              border-left: 0;
              left: 6px;
              top: 4px;
              transform: rotate(var(--r, 20deg));
            }
            &:checked {
              --r: 43deg;
              border-color: #2196f3;
            }
          }
          &.switch {
            width: 38px;
            border-radius: 11px;
            &:after {
              left: 2px;
              top: 2px;
              border-radius: 50%;
              width: 15px;
              height: 15px;
              background: var(--ab, var(--border));
              transform: translateX(var(--x, 0));
            }
            &:checked {
              --ab: var(--active-inner);
              --x: 17px;
            }
            &:disabled {
              &:not(:checked) {
                &:after {
                  opacity: 0.6;
                }
              }
            }
          }
        }
      }
    }
  
    .filter-item:hover {
      background-color: #f1fcff;
      cursor: pointer;
    }
  }
} 

